.webmain .con {
  margin-bottom: 45px; }

.webmain .con .title {
  height: 60px;
  overflow: hidden;
  background: url("../../../images/line_contitle.gif") center center repeat-x;
  margin-bottom: 15px;
  position: relative;
  float: left; }

.webmain .con .title a.more {
  display: block;
  position: absolute;
  right: 0;
  top: 15px;
  background: url("../../../images/more_contitle.gif");
  width: 130px;
  height: 30px;
  text-indent: -99em;
  overflow: hidden; }

.webmain .con .title h2 {
  display: block;
  height: 60px;
  background-image: url("../../../images/txt_contitle.gif");
  background-repeat: no-repeat;
  text-indent: -99em;
  overflow: hidden;
  float: left; }

.webmain .con .title ul {
  float: left;
  height: 26px;
  background: #fff;
  padding: 17px 5px; }

.webmain .con .title ul li {
  float: left;
  font-size: 16px;
  height: 26px;
  line-height: 26px;
  overflow: hidden;
  margin-right: 8px; }

.webmain .con .title ul li a {
  display: block;
  width: 100%;
  height: 26px;
  line-height: 26px;
  cursor: pointer;
  padding: 0 8px; }

.webmain .con .title ul li.c a {
  background: #000;
  color: #fff; }

.navTitle {
  color: #ccc;
  position: relative; }

.navTitle a {
  color: inherit; }

.navTitle a:hover {
  color: red; }

.tabctrl {
  position: absolute;
  bottom: 15px;
  left: 100px;
  width: 986px;
  height: 62px;
  background: url("../../../images/space.png");
  background: rgba(0, 0, 0, 0.5);
  filter: progid:DXImageTransform.Microsoft.gradient(startColorstr=#7F000000, endColorstr=#7F000000); }

.bannerlist,
.bannerlist li {
  position: relative;
  float: left; }

.tabctrl .text {
  display: inline-block;
  float: left;
  margin-top: 5px;
  width: 250px;
  font-size: 18px;
  font-weight: 700;
  color: #fff;
  line-height: 25px;
  padding-left: 20px; }

.bannerlist {
  width: 650px;
  height: 50px;
  margin-top: 6px;
  margin-left: 50px; }

.bannerlist li {
  display: inline-block;
  margin-right: 6px; }

.webmain .conTopbanner .bannerlist li img {
  width: 84px;
  height: 50px; }

.webmain .conTopbanner .bannerlist .frame {
  display: none;
  position: absolute;
  width: 76px;
  height: 42px;
  top: 0;
  left: 0;
  border: 4px solid #b162e4; }

.webmain .conTopbanner {
  position: relative;
  height: 470px;
  overflow: hidden; }

.webmain .conTopbanner img {
  display: block;
  width: 100%;
  height: 470px; }

.webmain .content {
  width: 770px;
  float: left;
  word-wrap: break-word;
  overflow: hidden; }
  .webmain .content img {
    display: inline; }

.webmain .rightCol {
  float: left;
  width: 380px; }

.webmain .con .title h2 {
  background: url("../../../images/favnews.jpg") 0 5px no-repeat;
  width: 200px; }

.webmain .con .newsList {
  float: left;
  width: 100%;
  margin-bottom: 30px; }

.webmain .con .newsList li {
  display: block;
  line-height: 30px;
  height: 30px;
  font-size: 12px; }

.webmain .con .newsList li .time {
  color: #ccc;
  float: right; }

.webmain .con .title {
  width: 100%; }

.webmain .con .title.event h2 {
  background: url("../../../images/icon-event.jpg") 0 5px no-repeat;
  width: 200px; }

.webmain .con .title.event
.webmain .con .active {
  float: left;
  font-size: 12px; }

.webmain .con .focus {
  float: left;
  height: 30px;
  color: #ccc;
  line-height: 30px;
  width: 100%; }

.webmain .con .focus .iconPart {
  display: block;
  float: right;
  width: 30px;
  height: 30px;
  background: url("../../../images/icon_parti.jpg") 50% no-repeat; }

.webmain .con .focus .iconFocus {
  display: block;
  float: right;
  width: 30px;
  height: 30px;
  background: url("../../../images/icon_focus.jpg") 50% no-repeat; }

.webmain .con .focus .text {
  float: right; }

.webmain .wrapper .content {
  margin-right: 50px; }

.webmain .wrapper .content .header {
  width: 100%;
  margin-bottom: 80px; }

.webmain .wrapper .content .header h1 {
  text-align: center;
  font-size: 24px;
  margin-bottom: 25px; }

.webmain .wrapper .content .header h3 {
  text-align: center;
  font-size: 14px;
  color: #a5a5a5; }

.webmain .con .item1 {
  margin-bottom: 10px;
  display: block; }
  .webmain .con .item1 img, .webmain .con .item1 sub {
    width: 380px;
    height: 250px; }
  .webmain .con .item1 .text {
    position: absolute;
    width: 380px;
    height: 60px;
    margin-top: -60px;
    color: #fff;
    text-indent: 20px;
    background: url("../../../images/space.png") 0 0 repeat;
    filter: progid:DXImageTransform.Microsoft.gradient(startColorstr=#99000000,endColorstr=#99000000);
    background: rgba(0, 0, 0, 0.6); }
    .webmain .con .item1 .text h1 {
      font-size: 14px;
      line-height: 30px; }
    .webmain .con .item1 .text h3 {
      font-size: 12px;
      line-height: 30px; }
  .webmain .con .item1 sub {
    position: absolute;
    margin-top: -250px;
    background: transparent;
    background: url("../../../images/space.png") 0 0 repeat; }
    .webmain .con .item1 sub:hover {
      background: rgba(0, 0, 0, 0.3);
      filter: progid:DXImageTransform.Microsoft.gradient(startColorstr=#4c000000,endColorstr=#4c000000); }

.webmain .con .item2 {
  margin-bottom: 10px;
  display: block; }
  .webmain .con .item2 img, .webmain .con .item2 sub {
    width: 380px;
    height: 285px; }
  .webmain .con .item2 .text {
    position: absolute;
    width: 380px;
    height: 60px;
    margin-top: -60px;
    color: #fff;
    text-indent: 20px;
    background: url("../../../images/space.png") 0 0 repeat;
    filter: progid:DXImageTransform.Microsoft.gradient(startColorstr=#99000000,endColorstr=#99000000);
    background: rgba(0, 0, 0, 0.6); }
    .webmain .con .item2 .text h1 {
      font-size: 14px;
      line-height: 30px; }
    .webmain .con .item2 .text h3 {
      font-size: 12px;
      line-height: 30px; }
  .webmain .con .item2 sub {
    position: absolute;
    margin-top: -285px;
    background: transparent;
    background: url("../../../images/space.png") 0 0 repeat; }
    .webmain .con .item2 sub:hover {
      background: rgba(0, 0, 0, 0.3);
      filter: progid:DXImageTransform.Microsoft.gradient(startColorstr=#4c000000,endColorstr=#4c000000); }
